/*--Functions--*/
async function submitForm(e, form) {
    // 1. Prevent reloading page
    e.preventDefault();
    // // 2. Submit the form
    // // 2.1 User Interaction
    const btnSubmit = document.getElementById('btnSubmit');
    btnSubmit.disabled = true;
    setTimeout(() => btnSubmit.disabled = false, 2000);
    // 2.2 Build JSON body
    var name = document.getElementById("name").value;
    var location = document.getElementById("location").value;
    var username = document.getElementById("username").value;
    var password = document.getElementById("password").value;
    var ip = document.getElementById("ip").value;
    var port = document.getElementById("port").value;
    var port1 = document.getElementById("port1").value;

    var channel = document.getElementById("channel").value;

    var src=`http://176.67.56.49:5000/getVideo/${username}/${password}/${ip}/${port}/${channel}01/hikvision`
    var snap=`http://176.67.56.49:5000/getSnap/${username}/${password}/${ip}/${port1}/${channel}01/hikvision`

    // console.log(src);
    // window.location=src;
    // var ip = document.getElementById("ip").value;

    const jsonFormData = buildJsonFormData(name,location,src,snap);

    // 2.3 Build Headers
    const headers = buildHeaders();
    // 2.4 Request & Response
    const response = await fetchService.performPostHttpRequest(`https://mongodb-tutorial-app.herokuapp.com/cameras/cam`, headers, jsonFormData); // Uses JSON Placeholder
    console.log(response);
    // 2.5 Inform user of result
    top.window.close();

    if(response)
        window.location = `/success.html`;
    else
        alert(`An error occured.`);
}

function buildHeaders(authorization = null) {
    const headers = {
        "Content-Type": "application/json",
        "Authorization": (authorization) ? authorization : "Bearer TOKEN_MISSING"
    };
    return headers;
}

function buildJsonFormData(name,location,src,snap) {
    const jsonFormData = { };
    jsonFormData['name'] =name;
    jsonFormData['location'] =location;
    jsonFormData['src'] =src;
    jsonFormData['snap'] =snap;

    return jsonFormData;
}
/*--/Functions--*/

